@font-face {
  font-family: "Kumbh Sans";
  src: url("../fonts/KumbhSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Kumbh Sans";
  src: url("../fonts/KumbhSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

* {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  font-family: "Kumbh Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 1;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 0;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  color: #2e3349; }

.header,
.footer {
  flex-shrink: 0; }

main {
  flex-grow: 1;
  overflow: hidden; }

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

button {
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #19A1ae;
  background-image: url("../img/oval-1.svg"), url("../img/oval-2.svg");
  background-repeat: no-repeat;
  background-position: 0 0 , 100% 100%;
  background-size: auto; }
  @media (max-width: 1023px) {
    .main {
      background-size: 70%; } }
  @media (max-width: 767px) {
    .main {
      padding: 0 24px;
      background-image: url("../img/oval-1-mobile.svg"), url("../img/oval-2-mobile.svg");
      background-size: auto; } }

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 350px;
  min-height: 374px;
  background-color: #ffffff;
  box-shadow: 0px 50px 100px -20px rgba(8, 70, 94, 0.504835);
  border-radius: 15px;
  overflow: hidden;
  z-index: 1; }
  @media (max-width: 767px) {
    .card {
      width: 100%;
      max-width: 326px; } }
  .card__wrap {
    position: relative;
    width: 100%; }
    .card__wrap::before {
      content: "";
      position: absolute;
      left: -238%;
      top: -182%;
      width: 978px;
      height: 976px;
      border-radius: 50%;
      opacity: 0.25;
      background: linear-gradient(180deg, rgba(9, 137, 180, 0.0001) 0.78%, #53FFEE 99.94%);
      mix-blend-mode: normal;
      z-index: -1;
      transform: rotate(-135deg); }
    .card__wrap::after {
      content: "";
      position: absolute;
      right: -220%;
      bottom: -218%;
      width: 978px;
      height: 976px;
      border-radius: 50%;
      opacity: 0.25;
      background: linear-gradient(180deg, rgba(9, 137, 180, 0.0001) 0.78%, #53FFEE 99.94%);
      mix-blend-mode: normal;
      z-index: -2;
      transform: matrix(0.71, -0.71, -0.71, -0.71, 0, 0); }
  .card__top {
    width: 100%;
    height: 140px;
    background-color: #41cbd3;
    background-image: url(../img/circles@1x.png);
    background-size: contain;
    background-repeat: no-repeat; }
    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      .card__top {
        background-image: url(../img/circles@2x.png); } }
  .card__bottom {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 0 48px; }
    .card__bottom::before {
      content: "";
      position: absolute;
      bottom: 85px;
      left: 0;
      width: 100%;
      height: 1px;
      border-bottom: 1px solid #e8eeec; }
  .card__general-info {
    display: flex;
    align-items: center;
    margin-bottom: 13px; }
  .card__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 25px 7px 25px 0; }
  .card__item {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .card__image {
    width: 106px;
    height: 106px;
    margin: -53px 0 19px;
    border-radius: 50%;
    border: 5px solid white;
    object-fit: cover;
    background-color: white; }
  .card__name {
    font-size: 18px;
    line-height: 1;
    margin: 0 9px 0 0; }
  .card__years {
    color: #6b7082; }
  .card__city {
    font-size: 14px;
    margin: 0;
    color: #6b7082;
    margin: 0 0 28px; }
  .card__item-title {
    font-size: 10px;
    font-weight: 400;
    letter-spacing: 1.5px;
    margin: 8px 0 0;
    color: #6b7082; }
  .card__number {
    font-weight: 700; }
